import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Grid from "@material-ui/core/Grid"
import Preview from "../static/apresentacoes/preview.jpg"
import Try from "../components/index/try"

const IndexPage = () => (
  <Layout>
    <SEO title="Apresentação online" />
    <Grid container
          direction="row"
          alignItems="center"
          justify="center">
      <Grid item xs={12} >
        <img src={Preview} alt="Apresentações exemplo"/>
      </Grid>
    </Grid>
    <Try />

  </Layout>
)

export default IndexPage
